	module.exports = {"data":{"markdownRemark":{"frontmatter":{"block":"MongoDB and Mongoose"},"html":"<h2>Introduction to the MongoDB and Mongoose Challenges</h2>\n<p>MongoDB is a database that stores data records (documents) for use by an application. Mongo is a non-relational, \"NoSQL\" database. This means Mongo stores all data associated within one record, instead of storing it across many preset tables as in a SQL database. Some benefits of this storage model are:</p>\n<ul>\n<li>Scalability: by default, non-relational databases are split (or \"sharded\") across many systems instead of only one. This makes it easier to improve performance at a lower cost.</li>\n<li>Flexibility: new datasets and properties can be added to a document without the need to make a new table for that data.</li>\n<li>Replication: copies of the database run in parallel so if one goes down, one of the copies becomes the new primary data source.</li>\n</ul>\n<p>While there are many non-relational databases, Mongo's use of JSON as its document storage structure makes it a logical choice when learning backend JavaScript. Accessing documents and their properties is like accessing objects in JavaScript.</p>\n<p>Mongoose.js is an npm module for Node.js that allows you to write objects for Mongo as you would in JavaScript. This can make is easier to construct documents for storage in Mongo.</p>\n<p>Working on these challenges will involve you writing your code on Glitch on our starter project. After completing each challenge you can copy your public glitch url (to the homepage of your app) into the challenge screen to test it! Optionally you may choose to write your project on another platform but it must be publicaly visible for our testing.</p>\n<p>Start this project on Glitch using <a href=\"https://glitch.com/#!/import/github/freeCodeCamp/boilerplate-mongomongoose/\">this link</a> or clone <a href=\"https://github.com/freeCodeCamp/boilerplate-mongomongoose/\">this repository</a> on GitHub! If you use Glitch, remember to save the link to your project somewhere safe!</p>\n<h2>Use mLab to host a free mongodb instance for your projects</h2>\n<p>For the following challenges, we are going to start using MongoDB to store our data. To simplify the configuration, we are going to use mLab.</p>\n<p>mLab is a MongoDB Database-as-a-Service platform, which basically means that they configure and host the database for you, making it so the only responsibility you have is to populate your database with what matters: data!\nWe are going to show you how to:</p>\n<ul>\n<li>Create an mLab account.</li>\n<li>Create a free online database.</li>\n<li>Create a new admin user on the database, so you can access it.</li>\n<li>Get the mLab URI, which you will use in your application to connect to your database.</li>\n</ul>\n<h3>Create an mLab account</h3>\n<p>Let's start by <a href='https://mlab.com/' target='_blank' rel='no-follow'>going to mLab</a>.</p>\n<p>Once you open the mLab page, you should sign up for a new account.</p>\n<ul>\n<li>Click the <a href='https://mlab.com/signup/' target='_blank' rel='no-follow'>Sign Up</a> button in the top right corner to open the registration page.</li>\n<li>Fill the registration form with your information and send it.</li>\n<li>You should be logged into your new, unverified account.</li>\n<li>In the top of the screen, a message should appear asking to send you an e-mail for account verification. Send and confirm it.</li>\n<li>After you confirm your account, click <strong>Create new</strong> in the <em>MongoDB Deployments</em> section.</li>\n</ul>\n<h3>Create a free online database.</h3>\n<p>Now we are going to create the actual database you are going to be using.</p>\n<ul>\n<li>Choose a <em>Cloud Provider</em> from the available list.</li>\n<li>Select the <em>Sandbox</em> plan type, which is the only one with no cost, and press <strong>Continue</strong>.</li>\n<li>Select a region for your Sandbox, from the available list, and press <strong>Continue</strong>.</li>\n<li>Input a name for your database. This name will be used in the URI for your database. After that, press <strong>Continue</strong>.</li>\n</ul>\n<p>A summary of all your choices should appear, allowing you to change any information provided in the previous steps. Press <strong>Submit Order</strong> to confirm the information.</p>\n<h3>Create a new admin user on the database</h3>\n<p>After you confirmed your configuration, a new sandbox should have been created in the <em>MongoDB Deployments</em> section. We are now going to create an administrator, so you can use the database in your application.</p>\n<ul>\n<li>Click the newly-created database.</li>\n<li>Click the <em>Users</em> section.</li>\n<li>Click the <strong>Add database user</strong> button.</li>\n<li>Input an username and password for your administrator. Do not mark it as read-only or you will not be able to add any information to the database with this user.</li>\n</ul>\n<h3>Get the mLab URI</h3>\n<p>Almost done! We have created our new database and created an user to access it, so we just need to find a way to use it in our applications.</p>\n<ul>\n<li>In your database page, you should see some instructions about connecting using the standard MongoDB URI.</li>\n<li>The line should look like this <code class=\"language-text\">mongodb://dbuser:dbpassword@ds0&lt;PORT&gt;.mlab.com:&lt;PORT&gt;/&lt;DATABASE-NAME&gt;</code>.</li>\n<li>Copy this URI and substitute dbuser and dbpassword with the information for the user you previously created in the database.</li>\n<li>That's it! This is the URI you will add to your application to connect to your database. Keep this URI safe somewhere, so you can use it later!</li>\n<li>Feel free to create separate databases for different applications, if they don't have an use for the same data. You just need to create the sandbox, user and obtain the new URI.</li>\n</ul>"},"allChallengeNode":{"edges":[{"node":{"fields":{"slug":"/apis-and-microservices/mongodb-and-mongoose/install-and-set-up-mongoose"},"title":"Install and Set Up Mongoose"}},{"node":{"fields":{"slug":"/apis-and-microservices/mongodb-and-mongoose/create-a-model"},"title":"Create a Model"}},{"node":{"fields":{"slug":"/apis-and-microservices/mongodb-and-mongoose/create-and-save-a-record-of-a-model"},"title":"Create and Save a Record of a Model"}},{"node":{"fields":{"slug":"/apis-and-microservices/mongodb-and-mongoose/create-many-records-with-model-create"},"title":"Create Many Records with model.create()"}},{"node":{"fields":{"slug":"/apis-and-microservices/mongodb-and-mongoose/use-model-find-to-search-your-database"},"title":"Use model.find() to Search Your Database"}},{"node":{"fields":{"slug":"/apis-and-microservices/mongodb-and-mongoose/use-model-findone-to-return-a-single-matching-document-from-your-database"},"title":"Use model.findOne() to Return a Single Matching Document from Your Database"}},{"node":{"fields":{"slug":"/apis-and-microservices/mongodb-and-mongoose/use-model-findbyid-to-search-your-database-by-id"},"title":"Use model.findById() to Search Your Database By _id"}},{"node":{"fields":{"slug":"/apis-and-microservices/mongodb-and-mongoose/perform-classic-updates-by-running-find-edit-then-save"},"title":"Perform Classic Updates by Running Find, Edit, then Save"}},{"node":{"fields":{"slug":"/apis-and-microservices/mongodb-and-mongoose/perform-new-updates-on-a-document-using-model-findoneandupdate"},"title":"Perform New Updates on a Document Using model.findOneAndUpdate()"}},{"node":{"fields":{"slug":"/apis-and-microservices/mongodb-and-mongoose/delete-one-document-using-model-findbyidandremove"},"title":"Delete One Document Using model.findByIdAndRemove"}},{"node":{"fields":{"slug":"/apis-and-microservices/mongodb-and-mongoose/delete-many-documents-with-model-remove"},"title":"Delete Many Documents with model.remove()"}},{"node":{"fields":{"slug":"/apis-and-microservices/mongodb-and-mongoose/chain-search-query-helpers-to-narrow-search-results"},"title":"Chain Search Query Helpers to Narrow Search Results"}}]}},"pathContext":{"block":"mongodb-and-mongoose","slug":"/apis-and-microservices/mongodb-and-mongoose"}}

